/* eslint react/prop-types: 0 */
/**
 * 整个react项目的入口文件App: 上下文无关性，通用性入口
 *  由client-entry 和 server-entry调用
 */

import React from 'react';
import { Provider } from 'react-redux';
import App from './components/app';

const Root = ({ store }) => (
  <Provider store={store}>
    <App />
  </Provider>
);

export default Root;



